.container {
  background-color: var(--bgc-primary);
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
  background-color: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  position: relative;
  overflow: hidden;
}

.header-box {
  width: 100%;
  align-items: center;
  padding: 18px 0;
  display: flex;
}

.nav-box {
  width: 100%;
  display: flex;
}

.nav {
  display: none;
}

.header-logo {
  width: 109px;
  height: 28px;
  align-items: center;
  gap: 8px;
  margin-right: 144px;
  display: flex;
}

.logo-svg {
  width: 24px;
  height: 24px;
}

.logo-text {
  fill: #000;
}

.theme-dark .logo-text {
  fill: #fff;
}

.theme-dark .header {
  background-color: var(--Darker);
  border-color: #fff;
}

.theme-dark .icon-menu, .theme-dark .icon-cross {
  stroke: #fff;
}

.bookshelf {
  width: 82px;
  height: 17px;
}

.nav-box-list.mobile-nav {
  display: none;
}

.mobile-panel {
  justify-content: space-between;
  align-items: center;
  gap: 14px;
  display: flex;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  -webkit-transition: all .4s;
  background-image: url("switcher.7ecf6097.svg");
  border-radius: 24px;
  transition: all .5s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider:before {
  content: "";
  height: 22px;
  width: 22px;
  background-image: url("slider.5934c140.svg");
  transition: all .5s;
  position: absolute;
  bottom: 0;
  left: -2px;
}

input:checked + .slider {
  background-image: url("switcher-on.cf91b838.svg");
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(21px);
}

.signup-button.mobile-signup {
  display: none;
}

.icon-menu, .icon-cross {
  stroke: var(--Darker);
}

.menu-button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: inline-flex;
}

.menu-button .icon-cross {
  display: none;
}

.menu-button.is-open .icon-cross {
  display: block;
}

.menu-button.is-open .icon-menu {
  display: none;
}

.icon-menu:hover, .icon-menu:focus, .icon-cross:hover, .icon-cross:focus, .mobile-menu-button:hover, .mobile-menu-button:focus {
  stroke: var(--Blue);
}

